            label: {
              rotate: 'tangential',
              fontSize: 10,
              color: colors[0]
            }
          },
          {
            r0: 200,
            r:220,
            itemStyle: {
              shadowBlur: 80,
              shadowColor: colors[0]
            },
            label: {
              position: 'outside',
              textShadowBlur: 5,
              textShadowColor: '#333'
            },
            downplay: {
              label: {
                opacity: 0.5
              }
            }
          }
        ]
      }
    ]
  };

myChart.setOption(option);

myChart.on('click',(param)=>{

  content.value=param.data;
  console.log(content.value.dis)
  if (content.value.dis===undefined){
    content.value.dis=content.value.name
  }
})
});

// 监听 value 的变化

</script>

