onMounted(async ()=>{
  await nextTick();
  var graph_chart_Container =await document.getElementById("model-kg-graph");

  const graph_chart=await echarts.init(graph_chart_Container);
  const radarChartContainer =document.getElementById("radar-chart-container");
  const pieChartContainer =document.getElementById("pie-chart-container");
  var pieChart=null
  var radarChart=null
  console.log(document.getElementById("model-kg-graph"));
  console.log(1)

  let option = {
    title: {
      text: '陶瓷品鉴知识图谱',
      left: 'left',
      textStyle: {
        color: 'rgba(29,29,30,0.73)',
        fontSize: 24,
        fontWeight: 'bold',
      }
      },
    tooltip: {},
    legend: [
      {
        data: graph_data.categories.map(c => ({
          name: c.name,
          icon: c.symbol // 使用每个分类的 symbol 作为图例的 icon
        })),
        top: '5%',  // 将图例向下偏移一点，避免紧贴顶部
        left: '1%',  // 将图例靠左放置
        orient: 'vertical',  // 图例排列方向为垂直
        itemWidth: 25,  // 图标的宽度
        itemHeight: 25, // 图标的高度
        textStyle: {
          fontSize: 14, // 设置字体大小
          color: '#333' // 设置字体颜色
        },
        padding: [10, 10, 10, 10],

      }
    ],
    animationEasingUpdate: "quinticInOut",
    series: {
      id: "graph",
      type: "graph",
      layout: "force",
      data: graph_data.nodes,
      links: graph_data.links,
      categories: graph_data.categories,
      roam: true,
      label: {
        // show: true,
        position: "right"
      },
      focusNodeAdjacency: true,
      legendHoverLink: true,
      animation: false,
      force: {
        initLayout: "circular",
        layoutAnimation: false,
        repulsion: 650, // 调整节点之间的排斥力，增大值会增加节点间的距离
        gravity: 0.1,   // 控制节点的聚集程度，值越大，节点越集中
        edgeLength: 100,
      },
      lineStyle: {
        width: 0.8, // 调整线条宽度为更细
        type: 'dashed',  // 设置虚线样式
        color: {
          type: 'linear', // 使用渐变色
          x: 0,
          y: 0,
          x2: 1,
          y2: 1,
          colorStops: [
            { offset: 0, color: 'rgba(255, 0, 0, 0.8)' },  // 起始点为红色
            { offset: 1, color: 'rgba(255, 100, 100, 0.4)' }   // 结束点为浅红色
          ],
          global: false  // 不使用全局渐变
        },
        opacity: 0.6, // 设置透明度
        curveness: 0.3, // 弯曲程度
        shadowColor: 'rgba(0, 0, 0, 0.3)', // 阴影颜色
        shadowBlur: 10, // 阴影模糊程度
      },
      emphasis: {
        focus: "adjacency",
        itemStyle: {
          shadowColor: "rgba(0, 0, 0, 0.4)",
          shadowBlur: 15
        },
        lineStyle: {
          width: 3
        },
        label: {
          textBorderColor: "rgba(255, 255, 255, 0.8)",
          textBorderWidth: 2
        }
      }
    }
  };
  await graph_chart.setOption(option);


// 获取右下角的雷达图容器

  radarChart = echarts.init(radarChartContainer);

  var radarOption = {
    color: ['#1E3A5F'],  // 深蓝色用于连线
    title: {
      text: '艺术特征分析',
      textStyle: {
        fontSize: 25,
      }
    },
    legend: {},
    radar: [
      {
        indicator: [
          { text: '形状复杂度', max: 100 },
          { text: '中外融合度', max: 100 },
          { text: '图案复杂度', max: 100 },
          { text: '色彩丰富度', max: 100 },
        ],

        radius: 120,
        startAngle: 90,
        splitNumber: 5,
        shape: 'circle',
        axisName: {
          formatter: '{value}',
          color: '#000000',  // 轴名称颜色改为黑色
          fontSize: 15  // 可选：设置字体大小
        },
        splitArea: {
          areaStyle: {
            color: [],  // 不填充任何颜色
            shadowColor: 'rgba(0, 0, 0, 0.2)',
            shadowBlur: 10
          }
        },
        axisLine: {
          lineStyle: {
            color: 'white',
            width: 2.0  // 轴线颜色为灰色
          }
        },
        splitLine: {
          lineStyle: {
            color: 'rgba(112, 128, 144, 0.2)',  // 偏灰的灰蓝色，透明度为 0.5
            width: 1.5  // 分割线宽度增加
          }
        }
      }
    ],
    series: [
      {
        type: 'radar',
        data: [
          {
            value: [20, 40, 70, 60],  // 示例数据，你可以根据需要填充实际数据
            areaStyle: {
              color: 'rgba(86, 163, 241, 0.3)'  // 填充颜色为浅蓝色，透明度为0.3
            },
            lineStyle: {
              color: '#1E3A5F',  // 连接线颜色为深蓝色
              width: 2
            },
            symbol: 'circle',  // 点的形状为圆形
            symbolSize: 6  // 点的大小设置为6
          }
        ]
      }
    ]
  };

// 渲染雷达图
  await radarChart.setOption(radarOption);
  pieChart =await echarts.init(pieChartContainer);

// 初始化饼图配置，扇形角度设置为 1/3（120度）
  var pieOption = {

    tooltip: {
      trigger: 'item',
    },
    series: [
      {
        type: 'pie',
        radius: '100%',
        startAngle: 150,
        data: [{ value: 20, itemStyle: { color: '#425380' } }, // 60% of 1/3
          { value: 13.33, itemStyle: { color: '#c9d2d5' } }, // 40% of 1/3
          { value: 66.66, itemStyle: { color: 'rgba(255, 255, 255, 0)' } }, // 2/3
        ],  // 初始为空
        label: {
          show: false,
        }
      }
    ]
  };

  await pieChart.setOption(pieOption);

  const nodesAction=(action, category)=> {
    let nodes = graph_chart.getOption().series[0].data;
    let names = [];

    if (category === "all") {
      // 如果是“全部”，则高亮所有节点
      names = nodes.map((node) => node.name);
    } else if (category !== "") {
      // 如果是特定类别，则筛选该类别的节点
      names = nodes
          .filter((node) => node.category === category)
          .map((node) => node.name);
    }

    graph_chart.dispatchAction({
      type: action,
      seriesIndex: 0,
      name: names
    });
  }
  const clickableNodes = [
    "元青花缠枝牡丹纹梅瓶",
    "清雍正景德镇窑粉彩蝠桃纹瓶",
    "霁红釉玉壶春瓶",
    "景德镇窑白釉雕花玲珑纹瓶"
  ];
  graph_chart.on('click', function (params) {
    const nodeName = params.data.name;
    if (clickableNodes.includes(nodeName)) {
      const node = graph_data.nodes.find(n => n.name === nodeName);

      if (node) {
        // Update the right panel with the node details
        document.getElementById('right-panel-image').src = node.image || '';
        document.getElementById('right-panel-name').innerText = node.name || '未知名称';
        document.getElementById('right-panel-description').innerText = node.description || '暂无描述';
        updateRadarChart(node.radarData);
        updatePieChart(node.pieData);
      }
    }
  });
  const updatePieChart=(pieData) =>{
    pieChart.setOption({
      series: [
        {
          type: 'pie',
          radius: '100%',
          startAngle: 150,
          data: pieData,
          label: {
            show: false,
          }
        }
      ]
    });
  }
  const simulateClickOnNode=(nodeName)=> {
    const node = graph_data.nodes.find(n => n.name === nodeName);
    if (node) {
      // 更新右侧面板的节点详细信息
      document.getElementById('right-panel-image').src = node.image || '';
      document.getElementById('right-panel-name').innerText = node.name || '未知名称';
      document.getElementById('right-panel-description').innerText = node.description || '暂无描述';
    }

    // 触发选中节点的样式
    graph_chart.dispatchAction({
      type: 'highlight',
      seriesIndex: 0,
      name: nodeName
    });
  }
  simulateClickOnNode("元青花缠枝牡丹纹梅瓶");
  const updateRadarChart=(radarData)=> {
    radarChart.setOption({
      series: [
        {
          type: 'radar',
          data: [
            {
              value: radarData,  // 这里使用传入的雷达数据
              areaStyle: {
                color: 'rgba(86, 163, 241, 0.3)'  // 填充颜色为浅蓝色，透明度为0.3
              },
              lineStyle: {
                color: '#1E3A5F',  // 连接线颜色为深蓝色
                width: 2
              },
              symbol: 'circle',  // 点的形状为圆形
              symbolSize: 6  // 点的大小设置为6

            }
          ]
        }
      ]
    });
  }

  var top1Sub = [], top3Sub = [], top5Sub = [];

  let nodes = graph_data.nodes;
  for (let c = 0; c < 6; c++) {
    let cnodes = nodes.filter((node) => node.category == c);
    cnodes.sort((a, b) => b.symbolSize - a.symbolSize);
    top1Sub.push(cnodes[0]);
    top3Sub.push(...cnodes.slice(0, 3));
    top5Sub.push(...cnodes.slice(0, 5));
  }

  var oldFocus = "";

  const changeFocus=(value)=> {
    if (value === "") {
      // 如果选择的是“全部”，则高亮所有节点
      nodesAction("highlight", "all");
    } else {
      // 如果选择的是其他类别，则高亮该类别的节点
      nodesAction("highlight", value);
      nodesAction("downplay", oldFocus);  // 去除之前高亮的节点
    }
    oldFocus = value;  // 更新当前高亮的节点类别
  }

  const switchSubgraph=(value)=> {
    let data = graph_data.nodes;
    switch (value) {
      case "1":
        data = top1Sub;
        break;
      case "3":
        data = top3Sub;
        break;
      case "5":
        data = top5Sub;
        break;
    }
    graph_chart.setOption({
      series: { data: data, zoom: 1 },
    });
  }

})




// Click event handler


