<template>
  <div class="common-layout" style="width: 80%;margin: auto">
    <div style="margin-bottom: 20px;font-size: 25px;line-height: 1.5em;font-weight: 900;color: rgba(29,29,30,0.68)">景德镇知名制瓷窑口分布图</div>
    <el-container>
      <el-aside width="40%" style="margin-right: 20px"><div id="map-container" style="height: 900px;"></div></el-aside>
      <el-main class="container_home" style="height: 900px;padding: 40px">
        <div class="info-box">
          <h2 class="title">{{data.name}}</h2>
          <div class="content">
            <p v-for="(item,index) in data.p" :key="index">{{item}}</p>
          </div>
        </div>
        <div class="image-gallery" style="margin-top: 40px">
          <h2 class="title">典型器物</h2>
          <el-carousel :interval="1500" type="card" height="200px">
            <el-carousel-item v-for="(item,index) in urls" :key="index">
              <img :src="item" alt="窑口1" height="200px" width="auto"/>
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import * as L from 'leaflet'
import 'leaflet/dist/leaflet.css';
import { onBeforeMount, onMounted, ref} from "vue";



const datas=ref([])


const data=ref({
  name:'',
  image:[],
  p:[],
  id:null

})
const urls=ref([])
onBeforeMount(async ()=> {
  const res = await fetch('./data.json').then(response => response.json())
  datas.value = res

  data.value = res[0]
  for (const item of data.value.image) {
    const url=await require('@/components/images/'+item);
    urls.value.push(url)
  }
})








onMounted(()=>{

  const map = L.map('map-container').setView([29.2700, 117.30], 9);


  // 添加 OpenStreetMap 图层
  L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png').addTo(map);

  // 加载景德镇边界的 GeoJSON 文件
  fetch('./geojson/360200.geojson')
      .then(response => response.json())
      .then(data => {
        L.geoJSON(data, {
          style: { color: 'blue', weight: 2, fillOpacity: 0 },
        }).addTo(map);
      });



  const markersData = [
    { position: [29.2695, 117.1789], info: '<b>崔公窑</b><br>青花瓷产地，具有地方特色' ,name:'崔公窑'},
    { position: [29.2728, 117.2220], info: '<b>厂官窑</b><br>以青花瓷和装饰性图案著称' ,name:'厂官窑'},
    { position: [29.2487, 117.2150], info: '<b>龙江窑</b><br>以龙纹为主题的瓷器',name:'龙江窑' },
    { position: [29.2745, 117.2582], info: '<b>蛤蟆窑</b><br>特有的釉色和青花瓷',name: '蛤蟆窑'},
    { position: [29.2399, 117.2511], info: '<b>壶公窑</b><br>主要生产壶形器和生活用器',name:'壶公窑' },
    { position: [29.2431, 117.1994], info: '<b>湖田窑</b><br>以精细的青花瓷著称' ,name:'湖田窑'},
    { position: [29.2752, 117.2012], info: '<b>军窑</b><br>专为军事相关器具生产瓷器' ,name:'军窑'},
    { position: [29.2655, 117.2234], info: '<b>柳家湾窑</b><br>精细的工艺和独特的装饰',name:'柳家湾窑' },
    { position: [29.2324, 117.1935], info: '<b>郎窑</b><br>精美瓷器和高艺术价值' ,name:'郎窑'},
    { position: [29.2798, 117.2268], info: '<b>马蹄窑</b><br>瓷器形状独特，像马蹄一样' ,name:'马蹄窑'},
    { position: [29.2846, 117.2359], info: '<b>石虎湾窑</b><br>青花和釉下彩瓷',name: '石虎湾窑'},
    { position: [29.2557, 117.2112], info: '<b>小南窑</b><br>工艺精细，色彩鲜艳',name: '小南窑'},
    { position: [29.2820, 117.2675], info: '<b>御土窑</b><br>为皇室提供皇家瓷器' ,name:'御土窑'},
    { position: [29.2380, 117.1694], info: '<b>永乐窑</b><br>永乐青花瓷，历史艺术价值高',name:'永乐窑' },
    { position: [29.2912, 117.2720], info: '<b>周窑</b><br>历史悠久，工艺精湛' ,name:'周窑'},
    { position: [29.2679, 117.2381], info: '<b>风火窑</b><br>特有的烧制技法和釉色' ,name:'风火窑'},
    { position: [29.2680, 117.2143], info: '<b>湘湖窑</b><br>艳丽釉色和独特装饰图案',name:'湘湖窑' },
    { position: [29.2672, 117.2407], info: '<b>臧窑</b><br>青花和粉彩瓷器' ,name:'臧窑'}
  ];
  markersData.forEach((markerData,index) => {
    const marker = L.marker(markerData.position).addTo(map);

    // 鼠标悬停时显示弹窗
    marker.on('mouseover', () => {
      marker.bindPopup(markerData.info).openPopup();
    });

    // 鼠标离开时关闭弹窗
    marker.on('mouseout', () => {
      marker.closePopup();
    });

    // 点击事件
    marker.on('click', (e) => {
      map.setView(markerData.position, 12);
      setData(index)


    });




})})

const setData=async (index:any)=>{
  data.value=datas.value[index];
  urls.value.length=0
  for (const item of data.value.image) {
    const url=await require('@/components/images/'+item);
    urls.value.push(url)
  }
}

</script>

