<template>
  <div id="use" style="width: 100%; height: 800px;"></div>
</template>

<script setup>
import { onMounted } from 'vue';
import * as echarts from 'echarts';

onMounted(() => {
  const myChart = echarts.init(document.getElementById('use'));

  // 使用 fetch 获取 JSON 数据
  fetch('./用途.json')
      .then(response => response.json())
      .then(data => {
        console.log(data);  // 调试输出，确保数据加载正常

        // 处理数据并生成树状图
        var treeData = {
          name: '陶瓷用途',
          children: [
            {
              name: '日用陶瓷',
              children: []
            }
          ]
        };

        var dailyCeramicsCategories = [
          '陶瓷酒器', '陶瓷茶器', '陶瓷炊器', '陶瓷餐器', '陶瓷文具', '陶瓷寝具', '陶瓷灯具', '陶瓷香器'
        ];

        const processedNodes = new Set();

        // 生成树状图数据
        data.data.datalist.forEach(item => {
          let category = item.SubLibraryName || '未分类';
          let nodeId = item.ID;

          if (processedNodes.has(nodeId)) {
            return;
          }

          processedNodes.add(nodeId);

          if (dailyCeramicsCategories.includes(category)) {
            let dailyCategory = treeData.children.find(c => c.name === '日用陶瓷');
            let existingSubCategory = dailyCategory.children.find(c => c.name === category);
            if (!existingSubCategory) {
              existingSubCategory = { name: category, children: [] };
              dailyCategory.children.push(existingSubCategory);
            }

            existingSubCategory.children.push({
              name: item.Name,
              description: item.SubcDescriptionText,
              enName: item.EnName,
              ID: nodeId
            });
          } else {
            let existingCategory = treeData.children.find(c => c.name === category);
            if (!existingCategory) {
              existingCategory = { name: category, children: [] };
              treeData.children.push(existingCategory);
            }

            existingCategory.children.push({
              name: item.Name,
              description: item.SubcDescriptionText,
              enName: item.EnName,
              url: item.URL,
              ID: nodeId
            });
          }
        });

        var option = {
          title: {
            text: '陶瓷用途与器具树状图',
            // subtext: '径向树状图展示陶瓷用途的层级结构',
            left: 'center',
            textStyle: {
              color: '#003366',
              fontSize: 24,
              fontWeight: 'bold',
            },
            subtextStyle: {
              color: '#003366',
              fontSize: 16
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: function (params) {
              var item = params.data;

              if (!item.children || item.children.length === 0) {
                return `
                <div class="tooltip-content">
                  <strong>${item.name}</strong> /${item.enName}/<br>
                  ${item.description}<br>
                </div>
              `;
              } else {
                return '';
              }
            }
          },
          series: [{
            type: 'tree',
            layout: 'radial',
            data: [treeData],
            roam: true,
            label: {
              position: 'top',
              verticalAlign: 'middle',
              fontSize: 14,
              color: '#003366'
            },
            itemStyle: {
              color: function(params) {
                return params.data.children ? '#cce0ff' : '#003366';
              },
              borderColor: '#003366',
              borderWidth: 2,
            },
            lineStyle: {
              color: '#003366',
              width: 1,
              type: 'solid'
            },
            emphasis: {
              focus: 'descendant'
            }
          }]
        };

        myChart.setOption(option);
      })
      .catch(error => {
        console.error('Error loading JSON:', error);
      });
});
</script>

<style scoped>
#use {
  width: 100%;
  height: 100%;
  background-color: #cce3dc; /* 背景使用温和的蓝色 */
}
html, body {
  height: 100%;
  margin: 0;
  font-family: 'Georgia', serif; /* 使用优雅的字体 */
}
#main {
  width: 100%;
  height: 100%;
  background-color: #f0f5ff; /* 背景使用温和的蓝色 */
}
.tooltip-content {
  font-size: 14px;
  color: #003366;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  border: 1px solid #003366; /* 边框使用深蓝色 */
}
.tooltip-content a {
  color: #0066cc;
  text-decoration: none;
}
/* 用途节点的样式 */
.category-node {
  background-color: #cce0ff;  /* 浅蓝色背景 */
  border-radius: 50%;
  border: 3px dashed #003366;  /* 虚线边框 */
  box-shadow: 0 0 12px rgba(0, 0, 255, 0.3);
}
.category-node:hover {
  background-color: #a3c9f7;  /* 悬停时深蓝色 */
  box-shadow: 0 0 18px rgba(0, 0, 255, 0.5);
}
/* 具体器具节点的样式 */
.item-node {
  background-color: #003366;  /* 深蓝色 */
  color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 255, 0.5);
}
.item-node:hover {
  background-color: #0052cc;  /* 深色器具悬停时变浅 */
  box-shadow: 0 0 18px rgba(0, 0, 255, 0.7);
}
</style>
